  searchShippingList(searchKey: string) {
    setTimeout(() => {
      if (searchKey.trim() === '') {
        this.getAllStockTransfer();
        this.isLoading = false; 
      } else {
        this.isLoading = false; 
        this.getAllStockTransfer(searchKey)
        if(this.shippingList.length === 0){
          this.isLoading = false; 
          this.toastService.toastMsg({
            title: "Error",
            content: searchKey + " Is Not Found",
          });
        }
      }
    this.isLoading = false; // Hide the loader after the API call is completed
    }, 500);
  }


